.container-slider {
 position:relative;
 width: 688px;
 height: 760px;
 left: 920px;
 top: 0px;
 overflow: hidden;

 
}
@media screen and (max-width: 700px){
  .container-slider {
    margin: 100px 10px 0;
  }
}
.slide {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
}
.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.active-anim {
  opacity: 1;
}

.btn-slide {
  width: 64px;
  height: 1000px;
  left: 615px;
 /* background: #0EC261;*/
  opacity: 0.01;
  border: 1px solid rgba(34, 34, 34, 0.287);
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
  
}
.btn-slide:hover{
  background: #0EC261;
  opacity: 0.3;
}
.btn-slide img {
  width: 25px;
  height: 25px;
  pointer-events: none;
  
}
.prev {
  top: 50%;
  left: 0px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.next {
  top: 50%;
  right: 0px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}



